.list {

}

.list ul {
    display: flex;
    list-style-type: none;
}

.list li {
    width: 100%;
    position: relative;
    padding-left: 20px;
}

.list ul li::before {
    position: absolute;
    content: "";
    display: inline-block;
    border-color: transparent transparent transparent #7d7789;
    border-style: solid;
    border-width: 5.5px 0 5.5px 9.5px;
    left: 0;
    top: 7px;
}